.background {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    overflow: hidden;
}

.background span {
    width: 62vmin;
    height: 62vmin;
    border-radius: 62vmin;
    backface-visibility: hidden;
    position: absolute;
    animation: move;
    animation-duration: 45;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    opacity: 0.2;
}

.background span:nth-child(0) {
    color: #da37d5;
    top: 40%;
    left: 22%;
    animation-duration: 30s;
    animation-delay: -6s;
    transform-origin: -6vw 21vh;
    box-shadow: -84vmin 0 21.06415726435833vmin currentColor;
}
.background span:nth-child(1) {
    color: #ffd77b;
    top: 34%;
    left: 28%;
    animation-duration: 20s;
    animation-delay: -23s;
    transform-origin: -9vw 1vh;
    box-shadow: 84vmin 0 20.506517386293924vmin currentColor;
}
.background span:nth-child(2) {
    color: #da37d5;
    top: 23%;
    left: 22%;
    animation-duration: 30s;
    animation-delay: -16s;
    transform-origin: 24vw -22vh;
    box-shadow: 84vmin 0 20.843331894187187vmin currentColor;
}
.background span:nth-child(3) {
    color: #da37d5;
    top: 25%;
    left: 31%;
    animation-duration: 20s;
    animation-delay: -21s;
    transform-origin: 9vw -6vh;
    box-shadow: 84vmin 0 21.071044369622303vmin currentColor;
}





