}
.marker {
  height: 16px;
  width: 16px;
  top: -7px;
  margin-left: -8px;
  background-color: grey;
  position: absolute;
  left: 2%;
  @include transition(left 0.5s ease-in-out);
  border-radius: 50%;
  background-image: linear-gradient(to right, rgba(218, 55, 213, 0.6) 0%, rgba(255, 215, 123, 0.6) 100%);
  border: 1px #EC85A9 solid;
}

#b:checked ~ .slider .marker {
  left: 50%;
}

#c:checked ~ .slider .marker {
  left: 98%;
}

